import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Login.css";
import axios from "axios";

const Login = (props) => {
  const [userId, setUserId] = useState("");
  const [userPw, setUserPw] = useState("");

  const onClickLogin = () => {
    console.log(props);

    axios
      .post(" http://localhost:8000/api/userLogin", {
        id: userId,
        password: userPw,
      })
      .then((response) => {
        console.log(response);
        if (response.data.success) {
          alert("로그인 성공");
          props.history.push("/postMain");
        } else {
          alert(response.data.error.message);
        }
      })
      .catch((Error) => {
        if (!Error.response.data.success) {
          alert(Error.response.data.error.message);
        }
      });
  };

  const onClickSignUpBtn = () => {
    console.log(props);
    props.history.push("/signUp");
  };

  const onChangeUserId = (e) => {
    setUserId(e.target.value);
  };

  const onChangeUserPw = (e) => {
    setUserPw(e.target.value);
  };

  return (
    <>
      <h2>로그인</h2>
      <div>
        아이디 : <input value={userId} onChange={onChangeUserId} />
        비밀번호 :{" "}
        <input type="password" value={userPw} onChange={onChangeUserPw} />
      </div>
      <button className="loginBtn" onClick={onClickLogin}>
        로그인
      </button>
      <button className="signUpBtn" onClick={onClickSignUpBtn}>
        회원가입
      </button>
    </>
  );
};

export default Login;
